		background-color: var(--teto-color);
	}

	.yufu {
		background-color: var(--yufu-color);
	}
}

/**Skinny screen**/
@media screen and (max-width:667px){
	.characters article {/**stops that 33% shit and just lets it take up the whole screen for skinny windows**/
		width: 100%;
	}
}

/**Short screen**/
@media screen and (max-height:667px){
	#intro {/**adds a lil more margin below the header**/
		margin-bottom: 5px;
	}
}